/*  default.less  */
// 
@gm-text-color-secondary: #697b8c;

// , labelinput
@line-height-base: 1.3;

// vertical paddings  xxs
@padding-xxs: 4px; // very small items

// 
@font-family: 'Microsoft YaHei', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'PingFang SC',
  'Hiragino Sans GB', 'Helvetica Neue', Helvetica, Arial, sans-serif, 'Apple Color Emoji',
  'Segoe UI Emoji', 'Segoe UI Symbol';

// input(bootstrap), 
@pagination-item-size: 34px;
@btn-height-base: 34px;
@input-height-base: 34px;

@label-color: @gm-text-color-secondary;

// , 
@form-item-margin-bottom: 16px;

//  25% -> 35%
@disabled-color: fade(#000, 35%);
@disabled-color-dark: fade(#fff, 45%);

// , , 
@gm-add-on-text-align: right;

/*  */
// 
.gm-title {
  color: @heading-color;
  font-weight: 600;
  font-size: 20px;
}

// 
.gm-title-lg {
  .gm-title;
  font-size: 24px;
}

// 
.gm-second-title {
  color: @gm-text-color-secondary;
  font-weight: 400;
  font-size: 16px;
}

// 
.gm-text {
  color: @text-color;
  font-weight: 400;
  font-size: 14px;
}

// 
.gm-second-text {
  .gm-text;
  color: @text-color-secondary;
}

// 
.gm-disabled-text {
  .gm-text;
  color: @disabled-color;
}

// 
.gm-error-text {
  .gm-text;
  color: @error-color;
}
